/*STYLE CALENDAR COMPONENTS*/
#calendars {
    grid-area: calendars;
    border-radius: 4px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: wrap;
        flex-flow: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    background-color: rgba(0, 0, 0, 0);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-top: 35px;
}

/*STYLE FALL AND WINTER CALENDARS*/
.eachCal {
    background-color: rgb(253, 254, 253);
    
    margin: 5px auto;
    font-family: 'Fira Sans', sans-serif;
    padding: 3px 2px;
    border-radius: 2px;
    display: grid;
    position: relative;
    grid-gap: 2px;
    grid-template-columns: 
        [start]             var(--colWidth) 
        [monday]            var(--colWidth) 
        [mondayConflict]    var(--colWidth) 
        [tuesday]           var(--colWidth) 
        [tuesdayConflict]   var(--colWidth) 
        [wednesday]         var(--colWidth) 
        [wednesdayConflict] var(--colWidth) 
        [thursday]          var(--colWidth) 
        [thursdayConflict]  var(--colWidth) 
        [friday]            var(--colWidth) 
        [fridayConflict]    var(--colWidth) 
        [end];
    grid-template-rows:
        [start]    var(--rowheight)
        [time]     var(--rowheight)
        [time8]    var(--rowheight)
        [time830]  var(--rowheight)
        [time9]    var(--rowheight)
        [time930]  var(--rowheight)
        [time10]   var(--rowheight)
        [time1030] var(--rowheight)
        [time11]   var(--rowheight)
        [time1130] var(--rowheight)
        [time12]   var(--rowheight)
        [time1230] var(--rowheight)
        [time13]   var(--rowheight)
        [time1330] var(--rowheight)
        [time14]   var(--rowheight)
        [time1430] var(--rowheight)
        [time15]   var(--rowheight)
        [time1530] var(--rowheight)
        [time16]   var(--rowheight)
        [time1630] var(--rowheight)
        [time17]   var(--rowheight)
        [time1730] var(--rowheight)
        [time18]   var(--rowheight)
        [time1830] var(--rowheight)
        [time19]   var(--rowheight)
        [time1930] var(--rowheight)
        [time20]   var(--rowheight)
        [time2030] var(--rowheight)
        [time21]   var(--rowheight)
        [time2130] var(--rowheight)
        [time22]   var(--rowheight)
        [time2230] var(--rowheight)
        [end];
    -webkit-box-shadow: 0px 10px 22px 2px rgba(118,122,128,.5);
    box-shadow: 0px 10px 22px 2px rgba(118,122,128,.5);
}

/*STYLE MO - FR TEXT*/
.weekday {
    grid-row: time / end;
    text-align:center;
    color: #111;
    font-size: var(--titleSize);
    font-weight: normal;
    padding-top: 5px;
}

.line {
    border-left: 1px solid #DDD;
    z-index: 2;
}

/*STYLE TIMES TEXT*/
.timeslot {
    font-size: var(--titleSize);
    color: #111;
    grid-column: start / end;
    padding-left: 4px;
    text-align: left;
}

.time {
    grid-column: start / end;
    text-align: left;
    padding-left: 4px;
}

.timeText {
    margin-top: 8px;
}

/*STYLE HALF HOUR ROWS*/
.halfHour {
    background-color: #F4F4F4;
    border-bottom: 1px solid #BDBDBD;
}

.first {
    border-top: 1px solid #BDBDBD;
}

.last {
    border-bottom: none;
}

/*STYLE TERM TEXT*/
.term {
    font-weight: bold;
    text-align: left;
    padding-left: 5px;
    color: #df744a;
}


