.entry {
  -webkit-box-shadow: 0px 4px 10px 1px rgba(118,122,128,.5 );
          box-shadow: 0px 4px 10px 1px rgba(118,122,128,.5 );
  z-index: 3;
  padding: 10px;
  border-radius: 2px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: start;
      align-items: flex-start;
  position: absolute;
  border: 1px solid #007887;
  width: 100%;
  height: 100%;
  top:-2px
}

.entryText {
  color: white;
  padding: 0;
  margin: 0;
  font-size: 80%;
}

.courseCode {
  font-size: var(--titleSize);
  font-weight: normal;
}

.removeEntry {
  border: none;
  background: none;
  position: absolute;
  bottom: 0;
  right: 0px;
  border: 1px solid transparent;
  -webkit-box-sizing: margin-box;
          box-sizing: margin-box;
  border-radius: 2px;
  -webkit-transition: all 250ms linear;
  -o-transition: all 250ms linear;
  transition: all 250ms linear;
}

.removeEntry:hover {
  background-color: pink;
  color: red;
  border: 1px solid red;
}



