/*STYLE ENTIRE SIDEBAR COMPONENT*/
#sidebar{
  grid-area: sidebar;
  background-color: white;
  position: fixed;
  margin: 0;
  height: 100vh;
  -webkit-box-shadow: 0px 10px 42px 2px rgba(118,122,128,.5);
  box-shadow: 0px 10px 42px 2px rgba(118,122,128,.5);
  overflow: auto;
  z-index: 5;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  width: var(--sidebarWidth);
  background-image: url("../../sidebar.jpg");
  background-size: auto auto;
    background-repeat: repeat-y;
}



