/*STYLE COUNTER*/
#counter{
  z-index: 400;
  position: fixed;
  top: 8px;
  right: 10px;
  height: 24px;
  min-width: 24px;
  color: #b91900;
  line-height: 24px;
  text-align: center;
  font-size: 26px;
  -webkit-transform-origin: 0 0;
      -ms-transform-origin: 0 0;
          transform-origin: 0 0;
  opacity: .4;
  text-shadow: -1px -1px 1px #FFB565, 1px 1px 1px #000;

}

/*DEFINE FLASH ANIMATIONS*/
@-webkit-keyframes flash1 {
    0% {
      color: #df744a;
      font-size: 46px;
      line-height: 48px;
      opacity: .3;
    }
    25% {
      color: #0ba7b7;
      opacity: 1;
    }
    50% {
      color: blue;
      opacity: .3;
    }
    100% {
      color: #0ba7b7;
      opacity: 1;
    }
}
@keyframes flash1 {
    0% {
      color: #df744a;
      font-size: 46px;
      line-height: 48px;
      opacity: .3;
    }
    25% {
      color: #0ba7b7;
      opacity: 1;
    }
    50% {
      color: blue;
      opacity: .3;
    }
    100% {
      color: #0ba7b7;
      opacity: 1;
    }
}

@-webkit-keyframes flash2 {
    0% {
      color: #df744a;
      font-size: 46px;
      line-height: 48px;
      opacity: .3;
    }
    25% {
      color: #0ba7b7;
      opacity: 1;
    }
    50% {
      color: blue;
      opacity: .3;
    }
    100% {
      color: #0ba7b7;
      opacity: 1;
    }
}

@keyframes flash2 {
    0% {
      color: #df744a;
      font-size: 46px;
      line-height: 48px;
      opacity: .3;
    }
    25% {
      color: #0ba7b7;
      opacity: 1;
    }
    50% {
      color: blue;
      opacity: .3;
    }
    100% {
      color: #0ba7b7;
      opacity: 1;
    }
}

.flash1, .flash2 {
    -webkit-animation-duration: 500ms;
            animation-duration: 500ms;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

.flash1 {
    -webkit-animation-name: flash1;
            animation-name: flash1;
}

.flash2 {
    -webkit-animation-name: flash2;
            animation-name: flash2;
}



